button {
    background: none;
    border:none;
    font-weight: bold;
    color: #6F1E51;
    cursor: pointer;
    padding: 3px 1rem 3px 0px ;
}

form {
    padding: 1rem 0px 1rem 0px;
    
}

label {
    margin: 8px 0px;
    display: inline-block;
}

input {
    width: 100%;
    display: block;
    box-sizing: border-box;
    margin: 0px 0px 8px 0px;
    padding: 6px 10px;

}


input[type=submit] {
    width:auto;
    margin: 12px 0px 5px 0px;
}