.card {
    display: flex;
    width:100%;
    max-width: 600px;
    margin: 5px auto;
    align-items: center;
    
}

.card:nth-child(odd) {
    background: rgba(127, 143, 166,0.3);
}

.card > div > div {
    padding: 1rem;
    width: 6rem;
    
}



.paid, .notpaid {
    margin-left: auto;
    text-align: center;
    width: 3.5rem;
    font-weight: bold;
    cursor: pointer;
}

.paid {
    color: #44bd32;
}

.notpaid {
    color: #e84118;
}

.delbtn {
    font-size: 0.8rem;
    color: #40739e;
    margin-top: 5px;
    cursor: pointer;
}