.balance {
    
    font-weight: bold;
    font-size: 2.5rem;
    padding: 1rem 0;
    color: #6F1E51;
    cursor: pointer;
}

.remaining {
    color:#353b48;
}

.perday {
    font-size: 0.8rem;
    padding: 0.5rem 0;
    color:#353b48;
}

h1,h2,h3 {
    text-align: center;
    margin: 0px;
    padding: 0px;

}

.HeaderCont {
    margin-top: 1.5rem;
    margin-bottom: 0.8rem;
}


.balinput {
    margin: 10px auto;
    text-align: center;
    display: block;
    border:0px;
    border-bottom: 2px solid #6F1E51;
    background-color: transparent;
    color:#6F1E51;
    font-size: 2rem;
    width: 60%;
    max-width: 15rem;
    font-weight: bold;
}

.balinput:focus{
    border:none;
    outline: none;
    border-bottom: 2px solid #6F1E51;
}